<template>
  <div class="receta-container">
    <h2>Recetas</h2>
    <!-- Botones principales -->
    <div class="buttons-container">
      <button @click="currentAction = 'elegir'">Elegir Receta</button>
      <button @click="currentAction = 'agregar'">Agregar Receta</button>
      <button @click="currentAction = 'eliminar'">Eliminar Receta</button>
    </div>

    <!-- Sección de agregar receta -->
    <div v-if="currentAction === 'agregar'" class="agregar-receta">
      <h3>Agregar Receta</h3>
      <input type="file" @change="handleFileUpload" />
      <button @click="uploadImage" :disabled="!file">Subir Imagen</button>

      <!-- Mostrar imagen cargada -->
      <div v-if="uploadedImageUrl" class="image-preview">
        <h4>Imagen cargada:</h4>
        <img :src="uploadedImageUrl" alt="Receta" class="uploaded-image" />
      </div>
    </div>

    <!-- Mensaje de placeholder para otras acciones -->
    <div v-if="currentAction === 'elegir'">
      <p>Esta sección mostrará las recetas disponibles para elegir.</p>
    </div>
    <div v-if="currentAction === 'eliminar'">
      <p>Esta sección permitirá eliminar recetas existentes.</p>
    </div>
  </div>
</template>

<script>
import {
  ref as storageRef,
  uploadBytes,
  getDownloadURL,
} from "firebase/storage";
import { storage } from "../firebase/firebase";

export default {
  data() {
    return {
      file: null, // Archivo seleccionado
      uploadedImageUrl: null, // URL de la imagen subida
      currentAction: "agregar", // Acción inicial por defecto
    };
  },
  methods: {
    handleFileUpload(event) {
      this.file = event.target.files[0];
      if (this.file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.uploadedImageUrl = e.target.result; // Vista previa de la imagen
        };
        reader.readAsDataURL(this.file);
      }
    },
    async uploadImage() {
      if (!this.file) return;

      const fileRef = storageRef(storage, `recetas/${this.file.name}`);
      try {
        await uploadBytes(fileRef, this.file);
        const url = await getDownloadURL(fileRef);
        this.uploadedImageUrl = url;
        alert("Imagen subida con éxito.");
      } catch (error) {
        console.error("Error al subir la imagen:", error);
        alert("Hubo un error al subir la imagen.");
      }
    },
  },
};
</script>

<style scoped>
.receta-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f9f9f9;
}

h2 {
  margin-bottom: 20px;
}

.buttons-container {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.agregar-receta {
  text-align: center;
}

.image-preview {
  margin-top: 20px;
}

.uploaded-image {
  max-width: 300px;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin: 10px 0;
}
</style>
